@import "../Default/variables.scss";

.isolation-content {
  background-color: $DarkBackground;
  padding-bottom: 50px;
  .covid {
    width: 40%;

    margin: 0px auto;
    padding: 50px 0 20px 0;
    text-align: center;
    color: white;
    line-height: 20px;
    font-size: 14px;
    min-width: 510px;
    box-sizing: border-box;
    @media only screen and (max-width: 600px) {
      width: 100%;
      padding: 30px;
      min-width: 200px;
    }
  }

  &-info {
    display: flex;
    margin: 0 auto;
    padding: 0 10px;
    max-width: 1000px;
    color: white;
    justify-content: space-between;
    flex-wrap: wrap;

    @media only screen and (max-width: 1024px) {
      flex-direction: column;
    }
    .content-block {
      box-sizing: border-box;

      width: 295px;

      display: flex;
      flex-direction: column;
      justify-content: center;
      background-color: $BoxPurple;
      padding: 20px 25px;
      @media only screen and (max-width: 1024px) {
        margin: 10px auto;
        width: 331px;
      }

      @media only screen and (max-width: 400px) {
        width: 298px;
      }
      .title {
        text-align: center;
        margin: 7px 0;

        font-weight: 900;
        font-size: 24px;
        letter-spacing: 1px;
        text-transform: uppercase;
      }

      .icon {
        width: 50%;
        height: 70px;
        margin: 0 auto;
      }

      .text {
        margin: 0px;

        color: rgba(255, 255, 255, 0.8);
        font-size: 12px;
        line-height: 18px;
      }
    }
  }

  .video-container {
    padding: 50px;
    width: 80%;
    margin: 0 auto;
    max-width: 1000px;
    @media only screen and (max-width: 500px) {
      padding: 50px 0;
    }
    .react-player {
      height: 500px !important;

      @media only screen and (max-width: 1024px) {
        height: 400px !important;
      }
      @media only screen and (max-width: 600px) {
        height: 260px !important;
      }
    }
  }
}
